    <div class="background"></div>
    <div class="main-container">
      <h1>IATA folder architecture creator</h1>
      <div class="text-container">
        <p>
          This application allows you to generate tha BAT file that creates the IATA folder architecture where it's executed.
        <br/><br/>  
          Fill in the values to get the correct folder names.
        </p>
      </div>
      <div class="form-container">
        <div class="input-container">
          <label for="ac-type-model">A/C type & model:</label>
          <input id="ac-type-model" type="text" placeholder="A350-941" v-model="variables.acTypeModel">
        </div>
        <div class="input-container">
          <label for="msn">MSN:</label>
          <input id="msn" type="text" placeholder="00145" v-model="variables.msn">
        </div>
        <div class="input-container">
          <label for="esn1">Engine 1 ESN:</label>
          <input id="esn1" type="text" placeholder="X45893" v-model="variables.esn1">
        </div>
        <div class="input-container">
          <label for="esn2">Engine 2 ESN:</label>
          <input id="esn2" type="text" placeholder="X45894" v-model="variables.esn2">
        </div>
        <div class="input-container">
          <label for="apusn">APU SN:</label>
          <input id="apusn" type="text" placeholder="PPA-897" v-model="variables.apusn">
        </div>
      </div>
      <div class="btns-container">
        <button class="button generate-btn" @click="generateBatFile" :disabled="!noFieldEmpty">GENERATE</button>
        <button class="button download-btn" @click="downloadFile" :disabled="batCode === ''">DOWNLOAD</button>
      </div>
    </div>
  </div>
</template>

